.assignments-container {
    display: flex;
    margin: 1rem 0;
}

.assignment {
    background-color: #791e94;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 1px rgba(0, 0, 0, 0.2);
    box-sizing: border-box;
    padding: 0.5rem 1rem;
    margin: 0.2rem 0.5rem;
}

.assignment p {
    font-size: 1.3rem;
}