.gym {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.gym-cells {
    display: flex;
}

.gym-tick {
    color: #2de2e6;
}

.gym-cross {
    color: #ff3864;
}

.gym-cell {
    width: 4rem;
    height: 4rem;
    background-color: #261447;
    margin: 1rem 0.5rem;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 1px rgba(0, 0, 0, 0.2);
    text-align: center;
    justify-content: center;
}

.gym-cell p {
    font-size: 1.3rem;
    padding-bottom: 0.5rem;
}