.card-container {
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    flex-wrap: wrap;
}

.card {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 20px 20px 12px;
    border-radius: 8px;
    margin: 1rem 0.5rem;
    /*background-color: #3c424b;*/
    background-color: #3c424b;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 1px rgba(0, 0, 0, 0.2);
}

