.quote {
    position: absolute;
    bottom: 1rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%)
}

blockquote {
    font-style: italic;
    margin: 0.25em 0;
    padding: 0.35em 40px;
    line-height: 1.45;
    position: relative;
}

blockquote:before {
    display: block;
    padding-left: 10px;
    content: "\201C";
    font-size: 80px;
    position: absolute;
    left: -20px;
    top: -20px;
    color: #ff6c11;
}

blockquote:after {
    content: "\0201D";
    display: block;
    padding-right: 10px;
    font-size: 80px;
    position: absolute;
    right: 20px;
    bottom: -20px;
    color: #ff6c11;
}